.component-loading {
  position: relative;
}

.component-loading:before {
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background: #f7f7f9;
  border-radius: 1px;
  opacity: 0;
  transition: opacity .1s ease-out;
  visibility: hidden;
  z-index: 1;
}

.component-loading.with-icon:after {
  content: "";
  font-family: FontAwesome;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  font-size: 2rem;
  justify-content: center;
  animation: fa-spin 1.5s infinite linear;
  color: #7a7f7f;
  opacity: 0;
  transition: opacity .1s ease-out;
  visibility: hidden;
  z-index: 10;
}

.component-loading.with-icon.refreshing:after {
  content: "\f021";
}

.component-loading.with-icon.sending:after {
  content: "\f1ce";
  animation: fa-spin 1s infinite linear;
}

.component-loading.isLoading:before {
  opacity: 0.5;
  visibility: visible;
}

.component-loading.isLoading:after {
  opacity: 1;
  visibility: visible;
}

